    align-self: flex-end;
}
.collection-sort {
    display: flex;
    flex-direction: column;
  }
  
  .collection-sort:first-child {
    padding-right: 20px;
    
  }
.collection-sort {
  display: flex;
  flex-direction: column;
  width: 12rem;
  margin: 10px;
  border: 0;
  padding: 0;
}

.collection-sort:first-child {
  padding-right: 20px;
}
.products {
  display: flex;
  flex-wrap: wrap;
}

.loader {
  border: 2rem solid #d80808;
  border-top: 2rem solid #3498db;
  border-radius: 50%;
  width: 120px;
  height: 120px;
  -webkit-animation: spin 2s linear infinite;
          animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.loader {
  justify-content: center;
}
.collection-sort {
  display: flex;
  flex-direction: column;
}

.collection-sort:first-child {
  padding-right: 20px;
}

.product-card {
